.hero{
    height: 100vh;
    background: linear-gradient(180deg,#fde1ff, #e1ffea22 60%);
    display: flex;
    }
.hero-left{
    flex:1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left: 180px;
    line-height:1.1;

}
.hero-left h2{
    display: flex;
    color: #090909;
    font-size: 26px;
    font-weight: 600;
}
.hero-left p{
    display: flex;
    color: #171717;
    font-size: 100px;
    font-weight: 700;
    margin: 0;
}
.hero-hand-icon{
    display: flex;
    align-items: center;
    gap: 10px;
}
.hero-hand-icon img{
    display: flex;
    flex-direction: column;
    width: 105px;

}
.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
    width: 310px;
    height: 70px;
    border-radius: 30px;
    margin-top: 30px;
    background: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500;
}
.hero-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}